import React, { useState } from 'react';
import { useLocation } from 'wouter';
import { login, storeToken } from '../lib/authService';

const translations: Record<string, Record<string, string>> = {
  en: {
    title: 'Login',
    identifier: 'Email or Mobile',
    password: 'Password',
    remember: 'Remember My Details',
    captcha: 'Captcha',
    submit: 'Login',
  },
  hi: {
    title: '',
    identifier: '  ',
    password: '',
    remember: '  ',
    captcha: '',
    submit: '',
  },
};

export default function LoginPage() {
  const [, setLocation] = useLocation();
  const [identifier, setIdentifier] = useState('');
  const [password, setPassword] = useState('');
  const [remember, setRemember] = useState(false);
  const [lang, setLang] = useState<'en'|'hi'>('en');
  const t = translations[lang];

  // simple math captcha
  const [a] = useState(() => Math.floor(Math.random()*9)+1);
  const [b] = useState(() => Math.floor(Math.random()*9)+1);
  const [captcha, setCaptcha] = useState('');

  const [error, setError] = useState<string | null>(null);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setError(null);
    if (parseInt(captcha || '0', 10) !== a + b) {
      setError('Invalid captcha');
      return;
    }

    try {
      const { token, user } = await login(identifier, password);
      storeToken(token, remember);
      // redirect based on role
      if (user.role === 'official') setLocation('/official');
      else setLocation('/dashboard');
    } catch (err: any) {
      setError((err && err.error) || 'Login failed');
    }
  }

  return (
    <div className="max-w-md mx-auto p-6">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-2xl font-semibold">{t.title}</h2>
        <div>
          <select value={lang} onChange={(e) => setLang(e.target.value as 'en'|'hi')} className="border px-2 py-1">
            <option value="en">EN</option>
            <option value="hi">HI</option>
          </select>
        </div>
      </div>
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label className="block text-sm">{t.identifier}</label>
          <input className="w-full border rounded px-3 py-2" value={identifier} onChange={e => setIdentifier(e.target.value)} />
        </div>
        <div>
          <label className="block text-sm">{t.password}</label>
          <input type="password" className="w-full border rounded px-3 py-2" value={password} onChange={e => setPassword(e.target.value)} />
        </div>
        <div>
          <label className="block text-sm">{t.captcha}: {a} + {b} = ?</label>
          <input className="w-full border rounded px-3 py-2" value={captcha} onChange={e => setCaptcha(e.target.value)} />
        </div>
        <div className="flex items-center gap-2">
          <input id="remember" type="checkbox" checked={remember} onChange={e=>setRemember(e.target.checked)} />
          <label htmlFor="remember">{t.remember}</label>
        </div>
        {error && <div className="text-sm text-red-600">{error}</div>}
        <div>
          <button className="bg-blue-600 text-white px-4 py-2 rounded" type="submit">{t.submit}</button>
        </div>
      </form>
      <div className="mt-4 text-sm">
        <a href="/signup" className="text-blue-600">Create account</a>
      </div>
    </div>
  );
}
