import React, { useState } from 'react';
import { useLocation } from 'wouter';
import { signup, storeToken } from '../lib/authService';

const translations: Record<string, Record<string, string>> = {
  en: {
    title: 'Sign Up',
    fullName: 'Full Name',
    identifier: 'Email or Mobile',
    password: 'Password',
    confirm: 'Confirm Password',
    village: 'Village Name',
    district: 'District',
    state: 'State',
    role: 'Select Role',
    submit: 'Sign Up'
  },
  hi: {
    title: ' ',
    fullName: ' ',
    identifier: '  ',
    password: '',
    confirm: '  ',
    village: '  ',
    district: '',
    state: '',
    role: ' ',
    submit: ' '
  }
}

export default function SignupPage() {
  const [, setLocation] = useLocation();
  const [fullName, setFullName] = useState('');
  const [email, setEmail] = useState('');
  const [mobileNumber, setMobileNumber] = useState('');
  const [password, setPassword] = useState('');
  const [confirm, setConfirm] = useState('');
  const [villageName, setVillageName] = useState('');
  const [district] = useState('');
  const [state] = useState('');
  const [role, setRole] = useState<'citizen'|'official'>('citizen');
  const [lang, setLang] = useState<'en'|'hi'>('en');
  const t = translations[lang];

  // simple captcha
  const [a] = useState(() => Math.floor(Math.random()*9)+1);
  const [b] = useState(() => Math.floor(Math.random()*9)+1);
  const [captcha, setCaptcha] = useState('');
  const [error, setError] = useState<string | null>(null);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setError(null);
    if (password !== confirm) return setError('Passwords do not match');
    if (parseInt(captcha || '0', 10) !== a + b) return setError('Invalid captcha');

    try {
      const payload: any = { fullName, password, villageName, role };
      if (email) payload.email = email;
      if (mobileNumber) payload.mobileNumber = mobileNumber;

      const { token, user } = await signup(payload);
      storeToken(token, true);
      if (user.role === 'official') setLocation('/official');
      else setLocation('/dashboard');
    } catch (err: any) {
      setError((err && err.error) || 'Signup failed');
    }
  }

  return (
    <div className="max-w-lg mx-auto p-6">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-2xl font-semibold">{t.title}</h2>
        <div>
          <select value={lang} onChange={(e) => setLang(e.target.value as 'en'|'hi')} className="border px-2 py-1">
            <option value="en">EN</option>
            <option value="hi">HI</option>
          </select>
        </div>
      </div>
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label className="block text-sm">{t.fullName}</label>
          <input className="w-full border rounded px-3 py-2" value={fullName} onChange={e=>setFullName(e.target.value)} />
        </div>
        <div>
          <label className="block text-sm">{t.identifier}</label>
          <input className="w-full border rounded px-3 py-2" value={email} onChange={e=>setEmail(e.target.value)} placeholder="you@example.com" />
          <div className="mt-2 text-xs text-gray-500">Or provide mobile number</div>
          <input className="w-full border rounded px-3 py-2 mt-1" value={mobileNumber} onChange={e=>setMobileNumber(e.target.value)} placeholder="+919XXXXXXXXX" />
        </div>
        <div>
          <label className="block text-sm">{t.password}</label>
          <input type="password" className="w-full border rounded px-3 py-2" value={password} onChange={e=>setPassword(e.target.value)} />
        </div>
        <div>
          <label className="block text-sm">{t.confirm}</label>
          <input type="password" className="w-full border rounded px-3 py-2" value={confirm} onChange={e=>setConfirm(e.target.value)} />
        </div>
        <div>
          <label className="block text-sm">{t.village}</label>
          <input className="w-full border rounded px-3 py-2" value={villageName} onChange={e=>setVillageName(e.target.value)} />
        </div>
        <div>
          <label className="block text-sm">{t.role}</label>
          <select value={role} onChange={e=>setRole(e.target.value as any)} className="w-full border rounded px-3 py-2">
            <option value="citizen">Citizen / User</option>
            <option value="official">Official</option>
          </select>
        </div>
        <div>
          <label className="block text-sm">Captcha: {a} + {b} = ?</label>
          <input className="w-full border rounded px-3 py-2" value={captcha} onChange={e=>setCaptcha(e.target.value)} />
        </div>
        {error && <div className="text-sm text-red-600">{error}</div>}
        <div>
          <button className="bg-green-600 text-white px-4 py-2 rounded" type="submit">{t.submit}</button>
        </div>
      </form>
    </div>
  );
}
